

.aside__wrapper{
    width: 100%;
    height: 100%;
    .aside__contant{
        display: flex;
        flex-direction: column;
        gap: 15px;
        margin-top: 60px;
    .aside__contant__item{
        width: 100%;
        height: 100%;
        background-position: center;
        background-repeat: no-repeat;
        padding: 10px;
        border: 1px solid #232323;
        border-radius: 10px;
        color: #232323;
        display: flex;
        flex-direction: column;
        gap: 10px;
        h3{
            font-size: 25px;
            font-family: "Oswald", sans-serif;
            line-height: 100%;
        }
        p{  
            font-size: 14px;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -moz-box;
            -moz-box-orient: vertical;
            display: -webkit-box;
            -webkit-line-clamp: 3;
            -webkit-box-orient: vertical;
            line-clamp: 3;
            box-orient: vertical;
        }
    }
}
@media (max-width:900px) {
    display: none;
}
}